import { Box } from 'grommet'
import { Cafeteria, Channel, Group, Halt, IceCream, Lounge, Restaurant } from 'grommet-icons'

function Itinerary({st}) {
    const setTemp = (value) => {
        st(value)
    }
  return (
    <Box
        background='red'
        height='large'
        id='itinerary'
        gap='medium'
        justify='center'
        align='center'
    >
        <Box
            border
            pad='medium'
            round
            direction='row'
            gap='medium'
            width='60%'
            elevation='medium'
            onClick={()=> setTemp('breakfast')}
        >
            <Cafeteria color='light-1'/>
            breakfast
        </Box>
        <Box
            border
            pad='medium'
            round
            direction='row'
            gap='medium'
            width='60%'
            elevation='medium'
            onClick={()=> setTemp('shows')}
        >
            <Channel color='light-1'/>
            shows
        </Box>
        <Box
            border
            pad='medium'
            round
            direction='row'
            gap='medium'
            width='60%'
            elevation='medium'
            onClick={()=> setTemp('lunch')}
        >
            <Restaurant color='light-1'/>
            lunch
        </Box>
        <Box
            border
            pad='medium'
            round
            direction='row'
            gap='medium'
            width='60%'
            elevation='medium'
            onClick={()=> setTemp('glam')}
        >
            <Halt color='light-1'/>
            glam nails
        </Box>
        <Box
            border
            pad='medium'
            round
            direction='row'
            gap='medium'
            width='60%'
            elevation='medium'
            onClick={()=> setTemp('relax')}
        >
            <Lounge color='light-1'/>
            relax
        </Box>
        <Box
            border
            pad='medium'
            round
            direction='row'
            gap='medium'
            width='60%'
            elevation='medium'
            onClick={()=> setTemp('family')}
        >
            <Group color='light-1' />
            family
        </Box>
        <Box
            border
            pad='medium'
            round
            direction='row'
            gap='medium'
            width='60%'
            elevation='medium'
            onClick={()=> setTemp('dinner')}
        >
            <IceCream color='light-1' />
            dinner
        </Box>
    </Box>
  )
}

export default Itinerary