import { Box, Heading, Image, Text } from 'grommet'
import glam from './assets/glam.jpg'
function Glam() {
  return (
    <Box
      pad={{vertical: 'medium' }}
    >
      <Box gap='medium' pad='medium'>
      <Heading color='red'>Glam Nails</Heading>
        <Text size='large'>WE DESERVE!</Text>
        <Text>Dont know how I found this place. But I love coming here with you. They make us feel special. And i love trying to make Jessica feel special. She desrves the things..including my </Text>
      </Box>
      <Box
        background='red'
        pad='large'
        gap='large'
      >
        <Box><Image src={glam} /></Box>
        </Box>
    </Box>
  )
}

export default Glam