import { Box, Button } from 'grommet'
import { Favorite } from 'grommet-icons'
import './Navbar.css'

function Navbar ({st}) {
    const setTemp = () => {
        st('welcome')
    }
    return (
        <Box 
            className='navb'
        >
            <Button icon={<Favorite color='light-1' />} href='#welcome' onClick={() => setTemp() }/>
        </Box>
    )
}

export default Navbar