import { Box, Heading, Image, Text } from 'grommet'
import relax from './assets/relax.jpg'
import relax2 from './assets/relax2.jpg'

function Relax() {
  return (
    <Box
      pad={{vertical: 'medium' }}
    >
      <Box gap='medium' pad='medium'>
        <Heading color='red'>Relax</Heading>
        <Text size='large'>I'm Le Tired</Text>
        <Text>Its been a long day already! But after a nice lunch and a bit of pampering, we can come home and get a good nap in!</Text>
      </Box>
      <Box
        background='red'
        pad='large'
        gap='large'
      >
        <Box><Image src={relax} /></Box>
        <Box><Image src={relax2} /></Box>
      </Box>
      <Box background='white' height='small'></Box>
    </Box>
  )
}

export default Relax