import { Box, Heading, Image, Text } from 'grommet'
import lux from './assets/luxx.jpg'

function Lunch() {
  return (
    <Box
      pad={{vertical: 'medium' }}
    >
      <Box gap='medium' pad='medium'>
        <Heading color='red'>Lunch</Heading>
        <Text size='large'>Jessicas Fav</Text>
        <Text>A place we always comes back too. The food is always great. The atmosphere is alwas beautiful but it doesn't have anyting on my best friend and love.</Text>
      </Box>
      <Box
        background='red'
        pad='large'
        gap='large'
      >
        <Box><Image src={lux} /></Box>
        </Box>
    </Box>
  )
}

export default Lunch